import React, { useEffect, useState } from 'react'
import Navbar from './components/Navbar'
import PromoSection from './components/PromoSection'
import Beams from './components/Beams'
import FormSection from './components/FormSection'
import InfoSection from './components/InfoSection'
import Slider from './components/Slider'
import MobileMenu from './components/MobileMenu'
import Loader from './components/Loader'
import LocationSection from './components/LocationsSection'
import { GlobalStyle } from './globalStyle'
import styled from 'styled-components'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { CSSRulePlugin } from 'gsap/CSSRulePlugin'
import ArticleSection from './components/ArticleSection'

gsap.registerPlugin(ScrollTrigger, CSSRulePlugin)


const Wrapper = styled.div`
  position: relative;
`

function App() {

  const [isOpen, setIsOpen] = useState(false);
  const [isLock, setIsLock] = useState(false);

  const rootElement = document.documentElement;
  
  // PRELOADER
  (function() {
    function selector(v) {
      return document.querySelector(v);
    }
    function loadbar() {
      let ovrl = selector('.preloader'),
          stat = selector('.percent'),
          img = document.images,
          c = 0,
          tot = img.length;
      

      function imgLoaded() {
        c += 1;
        var perc = ((100 / tot * c) << 0) + '%';

        stat.innerHTML = perc;
        if (c === tot) return doneLoading();
      }
      function doneLoading() {
        
        ovrl.classList.add('done')

        setTimeout(() => {
          setIsLock(false)
          ovrl.style.zIndex = -1;
        }, 1000)
      }

      for (var i = 0; i < tot; i++) {
        var tImg = new Image();
        tImg.onload = imgLoaded;
        tImg.onerror = imgLoaded;
        tImg.src = img[i].src;
      }
    }

    document.addEventListener('DOMContentLoaded', loadbar, false);
  })();
  // ===

  useEffect(() => {

    isOpen ? setIsLock(true) : setIsLock(false)
    isLock ? rootElement.classList.add('lock') : rootElement.classList.remove('lock')

  }, [isOpen, isLock, rootElement])

  useEffect(() => {

    // LETTERS CHANGE COLOR
    const heading = document.querySelector('.promo-heading span')
    const charsArray = heading.textContent.trim().split('')
    heading.textContent = ''

    let firstRow = 15
    let secondRow = 16
    let thirdRow = 12
    let paddingValue = 6

    let totalPaddingFirstRow = firstRow * paddingValue;
    let totalPaddingSecondRow = secondRow * paddingValue;
    let totalPaddingThirdRow = thirdRow * paddingValue;

    for(let i = 1; i <= charsArray.length; i++) {
        heading.innerHTML += `<span class="char char-${i}">${charsArray[i - 1]}</span>`
    }

    for(let i = 1; i <= firstRow; i++) {
      totalPaddingFirstRow -= paddingValue
      document.querySelector(`.char-${i}`).style.paddingTop = totalPaddingFirstRow +'px'
    }
    for(let i = secondRow; i <= 30; i++) {
      totalPaddingSecondRow -= paddingValue
      document.querySelector(`.char-${i}`).style.paddingTop = totalPaddingSecondRow +'px'
    }
    for(let i = 31; i <= 36; i++) {
      totalPaddingThirdRow -= paddingValue
      document.querySelector(`.char-${i}`).style.paddingTop = totalPaddingThirdRow +'px'
    }

    gsap.utils.toArray('.char').forEach(char => {

      ScrollTrigger.create({
        trigger: char,
        start: '-=80',
        end: '+=500',
        toggleClass: 'light'
      })

    })
    // ===

  }, [])

  return (
    <>
      <Loader />
      <GlobalStyle />
      <MobileMenu isOpen={isOpen} />
      <Navbar menuStatus={isOpen} menuHandler={setIsOpen} />
      <Wrapper className="page-wrapper">
        <PromoSection />
        <Beams />
        <InfoSection />
        <ArticleSection />
        <Slider />
        <LocationSection />
        <FormSection />
      </Wrapper>
    </>
  );
}

export default App;
