import React, { useEffect } from 'react'
import SectionHead from './elements/SectionHeading'
import Slide from './elements/Slide'
import { ContainerFluid } from '../globalStyle'
import { slidesData } from '../data/SlidesData'
import styled from 'styled-components'
import gsap from 'gsap'

const SliderBody = styled.div`
    width: ${({ slidesCount }) => slidesCount ? slidesCount+'00%' : '100%'};
    display: flex;
    flex-wrap: nowrap;
`

// width: ${({ slidesCount }) => slidesCount ? 'calc(' + slidesCount+'00% + ' +slidesCount*20+ 'px)' : '100%'};

const Slider = () => { 

    useEffect(() => {

        let slides = gsap.utils.toArray(".slide");

        gsap.to(slides, {
            xPercent: -100 * (slides.length - 1),
            ease: "none",
            scrollTrigger: {
                trigger: ".slider-section__body",
                pin: true,
                scrub: 1,
                // snap: {
                //     snapTo: directionalSnap(1 / (slides.length - 1)),
                //     duration: 1
                // },
                start: "-=50",
                end: "+=1000"
            }
        });

        // function directionalSnap(increment) {
        //     let snapFunc = gsap.utils.snap(increment);

        //     return (raw, self) => {
        //         let n = snapFunc(raw);
        //         return Math.abs(n - raw) < 1e-4 || (n < raw) === (self.direction < 0) ? n : self.direction < 0 ? n - increment : n + increment;
        //     };
        // }

    }, [])

    return (
        <div className="slider-section section">
            <ContainerFluid className="_container">
                <SectionHead offset={80}>
                До Ваших послуг персональні тренери - діючі плавці, призери України та Європи
                </SectionHead>
                <SliderBody slidesCount={slidesData.length} className="slider-section__body">
                    {slidesData.map(data =>  <Slide data={data} key={data.id} />)}
                </SliderBody>
            </ContainerFluid>
        </div>
    )
}

export default Slider